/* eslint-disable max-lines */
/* eslint-disable react/prop-types */
import React, { useRef } from 'react';
import PropTypes from 'prop-types';
import { convertPropToCssClassName } from '@elliemae/ds-classnames';
import { cx } from '@elliemae/ds-utilities/utils';
import CheckMark from './elements/CheckMark';
import { CHECKBOX_VARIANT, checkboxVariants } from '../../utils/prop-types';

function Checkbox({
  className,
  onChange,
  checked,
  innerRef,
  onKeyDown = () => null,
  readOnly,
  value,
  disabled,
  style,
  variant,
  tabIndex = 0,
  name,
  onMouseEnter,
  onMouseLeave,
  ...rest
}) {
  const checkRef = useRef(null);
  return (
    <span onMouseEnter={onMouseEnter} onMouseLeave={onMouseLeave}>
      <CheckMark
        {...rest}
        ref={innerRef}
        data-testid={rest['data-testid'] || 'ds-checkbox'}
        aria-checked={checked}
        aria-disabled={disabled}
        className={`${className} ${variant || ''} ${
          disabled ? 'disabled' : ''
        }`}
        data="checkmark"
        onClick={(e) => {
          e.target = checkRef.current;
          if (readOnly) return;
          onChange(e);
        }}
        onKeyDown={(e) => {
          e.target = checkRef.current;
          if (readOnly) return;
          if (e.key === 'Enter' || e.keyCode === 32) {
            onChange(e);
            onKeyDown(e);
          }
        }}
        role="checkbox"
        style={style}
        tabIndex={tabIndex}
        value={value}
      >
        {/* todo: I want to remove this and add an abstraction for this kind of input. @marc */}
        {/* check reakit checkbox */}
        {/* https://github.com/reakit/reakit/blob/master/packages/reakit/src/Checkbox/Checkbox.ts */}
        <input
          ref={checkRef}
          checked={checked}
          disabled={disabled}
          name={name}
          readOnly={readOnly}
          style={{ visibility: 'hidden', pointerEvents: 'none' }}
          type="checkbox"
          value={value}
          onChange={() => {}} // this suppress the warning, we manage onChange on another item level
        />
      </CheckMark>
    </span>
  );
}

const DSCheckbox = ({
  labelText = undefined,
  className = undefined,
  htmlFor = undefined,
  hasError = false,
  readOnly = false,
  disabled = false,
  checked = false,
  onChange = () => null,
  variant = CHECKBOX_VARIANT.DEFAULT,
  children = null,
  containerProps = {},
  innerRef,
  name = '',
  ...otherProps
}) => {
  const { cssClassName, classNameElement } = convertPropToCssClassName(
    'form-element-checkbox',
    className,
    {
      hasError,
      readOnly,
      disabled,
    },
  );

  const checkbox = (
    <Checkbox
      {...otherProps}
      checked={checked}
      className={cx(
        `${classNameElement('input-button')} checkbox-${String(checked)}`,
        className,
      )}
      disabled={disabled}
      innerRef={innerRef}
      name={name}
      onChange={onChange}
      readOnly={readOnly}
      variant={variant}
    />
  );

  if (!labelText) return checkbox;

  return (
    <div className={`${cssClassName} ${variant}`} {...containerProps}>
      <label className={classNameElement('label')} htmlFor={htmlFor}>
        {checkbox}
        <span className={classNameElement('label-text')}>{labelText}</span>
      </label>
      {children && (
        <div className={classNameElement('checkbox-children')}>{children}</div>
      )}
    </div>
  );
};

DSCheckbox.propTypes = {
  containerProps: PropTypes.shape({}),
  /**
   * Displayable label for the checkbox
   */
  labelText: PropTypes.oneOfType([PropTypes.string, PropTypes.element]),
  htmlFor: PropTypes.element,
  /**
   * Whether the checkbox has error or not
   */
  hasError: PropTypes.bool,
  /**
   * Whether the checkbox is read only or not
   */
  readOnly: PropTypes.bool,
  /**
   * Whether the checkbox is disabled or not
   */
  disabled: PropTypes.bool,
  /**
   * Whether the checkbox is checked, mixed or unchecked
   */
  checked: PropTypes.oneOfType([PropTypes.oneOf(['mixed']), PropTypes.bool]),
  /**
   * Value that takes the checkbox if it is checked
   */
  value: PropTypes.oneOfType([
    PropTypes.string,
    PropTypes.number,
    PropTypes.object,
  ]),
  /**
   * [
   *  'variant-default',
   *  'variant-focus',
   *  'variant-active',
   *  'variant-disabled',
   *  'variant-error',
   * ]
   */
  variant: PropTypes.oneOf(checkboxVariants),
  /**
   * Optional
   */
  children: PropTypes.oneOfType([
    PropTypes.element,
    PropTypes.arrayOf(PropTypes.element),
  ]),
  onChange: PropTypes.func,
  name: PropTypes.string,
};

export { CHECKBOX_VARIANT };
export default DSCheckbox;
